"use server";

import db from "@/db";
import { requireRole } from "@/app/lib/requireRole";
import type { Session } from "@/app/lib/auth-client";
import { nanoid } from "nanoid";

export async function generateInvite(session: Session) {
  requireRole(session, "admin");

  const inviteCode = nanoid(10);
  await db.globalSettings.update({
    where: { id: 1 },
    data: { inviteCodes: inviteCode },
  });

  return `https://localhost:3000/register?invite=${inviteCode}`;
}
